* {
    font-family: 'Ubuntu', sans-serif;
    color: #333;
}

.root {
    display: flex!important;
    flex-direction: column;
    min-height: 100vh;
}

.content {
    padding: 24px 0;
    flex-grow: 1;
}
.header {
    padding: 16px 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
    background-color: #fff;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
}

.header-logo {
    display: flex;
    align-items: center;
    text-decoration: none;
}

.header-logo__img {
    width: 48px;
    height: 48px;
    margin-right: 16px;
}

.header-logo__title {
    margin: 0;
    color: #333;
}

@media screen and (max-width: 762px) {
    .header-logo {
        font-size: 20px;
    }
}
.header-basket-button {
    outline: none;
    border-radius: 8px;
    border: 1px solid rgb(255, 105, 0);
    padding: 8px 24px;
    overflow: hidden;
    background: rgb(255, 105, 0);
    cursor: pointer;
    display: flex;
    align-items: center;
    text-decoration: none;
}

.header-basket-button span {
    color: rgb(255, 255, 255);
    font-weight: 500;
    font-size: 16px;
}

.header-basket-button__divider {
    display: inline-block;
    height: 20px;
    width: 1px;
    opacity: 0.4;
    margin: 0px 12px;
    background: rgb(255, 255, 255);
}
.menu-section-title {
    font-weight: 500;
    font-size: 30px;
}

.menu-section-title:first-letter {
    text-transform:capitalize;
}

.menu-item-wrapper {
    margin-bottom: 36px!important;
}
.menu-item {
    display: flex;
    flex-direction: column;
    height: 100%;
}

.menu-item__img {
    width: 100%;
    height: auto;
    transition: all 0.3s ease;
}

.menu-item__img_hovered {
    transform: translateY(12px);
}

.menu-item__title {
    font-size: 20px;
    line-height: 24px;
    margin-bottom: 16px;
}

.menu-item__description {
    font-size: 14px;
    line-height: 20px;
    color: rgb(115, 121, 140);
    margin-bottom: 24px;
}

.menu-item__bottom {
    margin-top: auto;
    display: flex;
    justify-content: space-between;
}

.menu-item__price {
    font-size: 22px;
    font-weight: 500;
}

.menu-item__to-basket {
    color: rgb(255, 255, 255);
    outline: none;
    border-radius: 8px;
    border: 1px solid rgb(255, 105, 0);
    padding: 8px 24px;
    overflow: hidden;
    background: rgb(255, 105, 0);
    font-weight: 500;
    font-size: 16px;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    cursor: pointer;
}
.cart-stepper {
    display: flex;
    align-items: center;
}

.cart-stepper button {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 24px;
    width: 24px;
    color: rgb(255, 105, 0);
    border: 1px solid rgb(255, 105, 0);
    background-color: transparent;
    border-radius: 8px;
    outline: none;
    padding: 0;
    font-size: 20px;
    cursor: pointer;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}
.cart-stepper span {
    margin: 0 12px;
    font-size: 22px;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}
.cart-title {
    font-weight: 500;
    font-size: 30px;
    margin-bottom: 48px;
}

.cart-list .cart-item {
    border-bottom: 1px solid rgb(241, 242, 245);
}

.cart-list .cart-item:first-child {
    border-top: 1px solid rgb(241, 242, 245);
}

.cart-total {
    font-size: 21px;
    font-weight: 700;
}

.cart-total__label {
    margin-right: 24px;
}

.cart-total__quantity {
    color: rgb(255, 105, 0);
}

.cart-btn {
    font-weight: 500;
    font-size: 18px;
    border-width: 1px;
    border-style: solid;
    border-radius: 10px;
    outline: none;
    transition: all 0.15s ease 0s;
    padding: 18px;
    cursor: pointer;
    text-decoration: none;
}

.cart-btn_back-menu {
    border-color: rgb(186, 191, 208);
    background-color: transparent;
}
.cart-btn_back-menu:hover {
    color: rgb(255, 105, 0);
    border-color: rgb(255, 105, 0);
}

.cart-btn_make-order {
    color: rgb(255, 255, 255);
    border-color: rgb(255, 105, 0);
    background-color: rgb(255, 105, 0);
}
.cart-item {
    padding: 16px 0px;
}

.cart-item__img {
    width: 64px;
    height: 64px;
    margin-right: 16px;
}

.cart-item__title {
    font-size: 22px;
    line-height: 23px;
    font-weight: 500;
}

.cart-item-remove {
    border: none;
    background-color: transparent;
    outline: none;
    cursor: pointer;
    padding: 0;
}

.cart-item-remove img {
    width: 16px;
    height: 16px;
}

.cart-item-price {
    font-size: 20px;
    font-weight: 50;
    margin-right: 24px;
}
.delivery-item {
    padding: 16px 0px;
}

.delivery-item__img {
    width: 64px;
    height: 64px;
    margin-right: 16px;
}

.delivery-item__title {
    font-size: 22px;
    line-height: 23px;
    font-weight: 500;
}

.delivery-item-price {
    font-size: 20px;
    font-weight: 50;
    margin-right: 40px;
}
.checkout-button {
    font-weight: 500;
    font-size: 18px;
    border-width: 1px;
    border-style: solid;
    border-radius: 10px;
    outline: none;
    transition: all 0.15s ease 0s;
    padding: 18px;
    cursor: pointer;
    color: rgb(255, 255, 255);
    border-color: rgb(255, 105, 0);
    background-color: rgb(255, 105, 0);
}

.loader {
    height: 100px;
}

.loader-wrapper {
    padding: 85px 0;
    display: flex;
    align-items: center;
    justify-content: center;
}

.success-title {
    margin-bottom: 36px;
}
html {
  font-size: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Helvetica, Arial,
    sans-serif;
  font-size: 1rem;
  line-height: 1.5;
}

button:disabled {
  opacity: 0.5;
}

